<template>
    <div>
        <v-btn
            class="mx-2 right_20 top_85 mr-0 z-L"
            fab
            
            :color="color"
            :class="extra_calss"

            :fixed="is_fixed"

            :left="l_or_r"
            :right="!l_or_r"
            :x-small="is_small"

            :disabled="useless"

            @click="lockPrint"
        >
            <v-icon>
                {{ icon }}
            </v-icon>
        </v-btn>
        <div class="pdf-fixed-iframe" id="_data">
            <!-- pdf-user></pdf-user-->
            <iframe :src="link">
            </iframe>
        </div>

    </div>
</template>

<script>
import PdfUser from '../PdfUser/PdfUser.vue'
    export default {
  components: { PdfUser },
        name: '',
        props: {

            icon: {
                type: String,
                default: 'mdi-file-pdf-box-outline'
            },
            color: {
                type: String,
                default: ''
            },
            extra_calss: {
                type: String,
                default: ''
            },
            is_fixed: {
                type: Boolean,
                default: true
            },
            l_or_r: {
                type: Boolean,
                default: false
            },
            is_small: {
                type: Boolean,
                default: false
            },

            useless: {
                type: Boolean
            }
        },
        data() {
            return {
                print: false,

                link: 'https://fanyi.youdao.com/'
            }
        },
        methods: {
            lockPrint() {
                this.link = ''
                const uri = this.conf.baseURL + '/#/ui/pdf_user/?menu=false&unid=' + this.$store.state.unid
                this.link = uri
                console.log('URI =', uri)
            }
        },
    }
</script>

<style lang="sass" scoped>
.pdf-fixed-iframe
    position: fixed
    top: 20vh
    left: 0
    z-index: 999
    width: 100vw
    height: 30vh !important
    overflow: scroll
    // visibility: hidden

.pdf-fixed-iframe iframe
    width: 100%
</style>


